import React, { useEffect } from "react";

import "./EcommerceWebsite.css";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import ecommercewebsite from "./ECommerceBanner.jpg";

import Description from "../../../components/ecommerce_website_components/description/Description";

import EcommerceCardLeft from "../../../components/ecommerce_website_components/ecommerce_card_left/EcommerceCardLeft";

import EcommerceCardRight from "../../../components/ecommerce_website_components/ecommerce_card_right/EcommerceCardRight";

//import Description2 from "../../../components/ecommerce_website_components/description2/Description2";

import Pricing from "../../../components/services_page_components/pricing/Pricing";

import Footer from "../../../components/genral_components/Footer/Footer";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import pic1 from "./ecommOne.png"
import pic2 from "./ecommTwo.png"
import pic3 from "./ecommThree.png"

import { useLocation } from "react-router-dom";

export default function EcommerceWebsite() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className="e-commerce-website-design-main-component">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="E-commerce Website Design"
        bannerParagraph="Harness cutting-edge e-commerce website design to increase sales"
        bannerButton1="LET’S CHAT"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={ecommercewebsite}
      />

      <Description />

      <EcommerceCardLeft
        heading="Increased Conversions"
        paragraph="Professional, clean, and easy to use, along with clear product descriptions, reactive calls-to-action, and smooth checkout processes, can attract potential buyers, leading to higher conversion rates and more sales. A well-optimized custom e-commerce website development can help boost conversion rates and this improves the overall user experience."
        button="LET’S CHAT"
        picture={pic1}
      />

      <EcommerceCardRight
        heading="Brand Representation"
        paragraph="An E-commerce website serves as a virtual storefront for your business. It should be carefully crafted to align with your brand identity and helps convey professionalism, trustworthiness, and credibility. It should showcase your products in a consistent manner, reinforcing your brand image and creating a positive perception among customers."
        button="LET’S CHAT"
        picture={pic2}
      />

      <EcommerceCardLeft
        heading="Mobile Responsiveness"
        paragraph="Having a responsive e-commerce website design is important. This mobile-friendliness is crucial for attracting and retaining customers who prefer shopping on their smartphones or tablets."
        button="LET’S CHAT"
        picture={pic3}
      />

      {/* <Description2/> */}

      <br />
      <br />
      <br />
      
      <Pricing/>

      <br />
      <br />
      <br />

      <Skills/>

      <br />
      <br />
      <br />

      <Ourwork/>

      <Footer/>
    </div>
  );
}
