import React from "react";

import "./EcommerceCardRight.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function EcommerceCardRight(prop) {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="ecommerce-cards-right-main-container">
      <div className="ecommerce-cards-right-main-container-box-1">
        <div
          className={`ecommerce-cards-right-main-container-box-1a ${
            inViewBox1a ? "ecommerce-cards-right-slide-in-animation" : ""
          }`}
        >
          <h3>{prop.heading}</h3>

          <p ref={refBox1a}>{prop.paragraph}</p>

          <button>{prop.button}</button>
        </div>
      </div>

      <div className="ecommerce-cards-right-main-container-box-2">
        <img src={prop.picture} alt="pic1" />
      </div>
    </div>
  );
}

EcommerceCardRight.defaultProps = {
  heading: "Enter your heading here",
  paragraph: "Enter your text here",
  button: "Text for first button",
  picture: pic1,
};
