import React from "react";

import "./EcommerceCardLeft.css";

import pic1 from "./pic1.png";

import { useInView } from "react-intersection-observer";

export default function EcommerceCardLeft(prop) {
  const [refBox1a, inViewBox1a] = useInView({
    triggerOnce: true,
    rootMargin: "0px 0px",
  });

  return (
    <div className="ecommerce-card-left-main-container">
      <div className="ecommerce-card-left-main-container-box-1">
        <div
          className={`ecommerce-card-left-main-container-box-1a ${
            inViewBox1a ? "ecommerce-card-left-slide-in-animation" : ""
          }`}
        >
          {prop.heading && <h3>{prop.heading}</h3>}

          <p ref={refBox1a} >{prop.paragraph}</p>

          <button>{prop.button}</button>
        </div>
      </div>

      <div className="ecommerce-card-left-main-container-box-2">
        <img src={prop.picture} alt="pic1" />
      </div>
    </div>
  );
}

EcommerceCardLeft.defaultProps = {
  /* heading: "Enter your heading here", */
  paragraph: "Enter your text here",
  button: "Text for first button",
  picture: pic1,
};
